// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

body {
    font-family: $Montserrat;
}

* {
    box-sizing: border-box;
}

*:focus {
    outline: none;
}

main {
    width: 100%;
    max-width: 1366px;
    margin:  0 auto;
    display: flex;
}

.rightbar {
    min-width: 160px;
    background-color: #000;
}

.content {
    padding-right: 25px;
    .created {
        height: auto;
    }
}

.virtual-exhibition {
    background: url(../images/virtual-exhibition.png) no-repeat;
    margin-right: -25px;
    height: 768px;
    p {
        margin: 0;
        font-size: 18px;
        line-height: 36px;
        color: #8b8585;
    }
    img {
        box-shadow: none;
    }
}

.wrapper {
    padding: 40px;
    padding-right: 0;
}

.virtual-exhibition__wrapper {
    display: flex;
    height: 100%;
    padding-bottom: 80px;
    flex-direction: column;
    justify-content: space-between;
    .wrapper__top-part,
    .wrapper__bottom-part {
        width: 575px;
    }
    .wrapper__top-part p {
        font-weight: 600;
    }
    .wrapper__bottom-part p {
        font-weight: 300;
    }
}

.title h2 {
    font-size: 48px;
    line-height: 72px;
    font-weight: 500;
    position: relative;
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 160px;
        height: 4px;
        background: linear-gradient(to right, #3a3184, #c62885);
    }
}

.pink {
    color: #c52a85;
}

.flex-row {
    display: flex;
    .text {
        flex: 1;
        padding-right: 30px;
    }
    .image, .slider {
        width: 559px;
        display: block;
    }
    .image, .slider {
        img {
            max-width: 100%;
        }
    }
}

.slider img {
    box-shadow: none;
}

.text, .image {
    font-family: $Roboto;
    font-size: 14px;
    line-height: 26px;
    p {
        margin: 0 0 10px 0;
    }
}

#points {
    margin-right: -25px;
    padding-right: 25px;
    background-color: #e8e8f0;
    p {
        font-family: "Roboto", sans-serif;
        font-size: 14px;
        line-height: 26px;
        margin: 0 0 10px 0;
    }
    ul {
        padding: 0;
        list-style: none;
        margin-top: 40px;
        li {
            margin-bottom: 60px;
        }
    }
    li p {
        font-weight: 500;
        font-family: $Montserrat;
        font-size: 18px;
        margin-bottom: 30px;
        position: relative;
        text-indent: 20px;
        &:before {
            content: '';
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: #c52a85;
            position: absolute;
            left: 0;
            top: calc(50% - 3px);
        }
    }
    button {
        font-family: $Montserrat;
        text-transform: uppercase;
        font-weight: 700;
        background: #000;
        color: #962b85;
        border: none;
        height: 57px;
        width: 320px;
        border-top: 4px solid transparent;
        border-bottom: 4px solid transparent;
        border-image: linear-gradient(to right, #3a3184, #c62885);
        border-image-slice: 1;
        transition: .2s;
        &:hover {
            color: #3a3184;
        }
    }
}

img {
    box-shadow: 0 9px 27px rgba(0,0,0,.06);
}

.callback {
    margin-bottom: 30px;
}

.callback form {
    width: 480px;
    label, input[type=text], input[type=email], textarea {
        width: 100%;
    }
    label {
        font-size: 18px;
        margin-bottom: 10px;
        display: block;
    }
    input[type=text], input[type=email], textarea {
        border: none;
        border-radius: 4px;
        background-color: #f4f5f8;
        font-weight: 300;
        font-size: 14px;
        margin-bottom: 35px;
    }
    input[type=text], input[type=email] {
        height: 42px;
        padding: 0 15px;
    }
    textarea {
        resize: none;
        height: 160px;
        padding: 10px 15px;
    }
    input[type=submit] {
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        border: none;
        background-color: #000;
        color: #fff;
        width: 240px;
        height: 50px;
        cursor: pointer;
        &:hover {
            box-shadow: 0 9px 27px rgba(0, 0, 0, 0.06);
        }
    }
}

.flex-row .callback__image {
    width: auto;
    flex: 1;
    text-align: right;
}

.created__wrapper {
    padding: 0 0 20px 40px;
}

.created__flex-row {
    justify-content: flex-end;
    a {
        text-decoration: none;
        border-bottom: 1px solid transparent;
        border-image: linear-gradient(to right, #cac8df, #efc6de);
        border-image-slice: 1;
        background: linear-gradient(to right, #3a3184, #c62885);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

.lSAction > a {
    top: auto;
    bottom: -20px;
    background: none;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    .icon {
        color: #000;
        font-size: 15px;
    }
    &:hover {
        background-color: #c52a85;
        .icon {
            color: #fff;
        }
    }
}

.lSSlideOuter {
    padding-bottom: 40px;
    position: relative;
}

.lSSlideWrapper {
    overflow: visible;
}

.lSAction > .lSNext {
    right: 0;
}
.lSAction > .lSPrev {
    left: auto;
    right: 35px;
}

.lSSlideOuter .lSPager.lSpg>li a {
    background-color: #3c3184;
}

.lSSlideOuter .lSPager.lSpg>li.active a, .lSSlideOuter .lSPager.lSpg>li:hover a {
    background-color: #c52a85;
    transform: scale(1.2);
}

.lSSlideOuter .lSPager.lSpg {
    position: absolute;
    width: 20px;
    height: calc(100% - 40px);
    right: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 !important;
}

.rightbar .menu {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    nav {
        flex: 1;
    }
    a {
        display: block;
        font-size: 14px;
        font-weight: 300;
        color: #6a6a6a;
        text-decoration: none;
        padding: 19px 13px;
        text-align: right;
        &.active, &:hover {
            background: linear-gradient(to right, #3a3184, #c62885);
            color: #fff;
            text-shadow: 0 0 1px #fff;
        }
    }
    .buttons-group {
        display: flex;
    }
    button {
        width: 50%;
        height: 46px;
        border: none;
        background: none;
        color: #6a6a6a;
        position: relative;
        font-size: 13px;
        display: flex;
        align-items: center;
        &.back {
            visibility: hidden;
        }
        &.next {
            justify-content: flex-end;
        }
        &:hover {
            color: #fff;
            background: linear-gradient(to right, #3a3184, #c62885);
        }
    }
}

.rightbar {
    position: relative;
}
.rightbar__wrapper {
    position: absolute;
    width: 100%;
    left: 0;
}
.rightbar .menu {
    position: fixed;
    width: 160px;
}

