// stylelint-disable no-empty-source
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap');

@font-face {
    font-family: 'icomoon';
    src:  url('../fonts/icomoon.eot?t6f4jg');
    src:  url('../fonts/icomoon.eot?t6f4jg#iefix') format('embedded-opentype'),
      url('../fonts/icomoon.ttf?t6f4jg') format('truetype'),
      url('../fonts/icomoon.woff?t6f4jg') format('woff'),
      url('../fonts/icomoon.svg?t6f4jg#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
  
  [class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .icon-chevron-right:before {
    content: "\e901";
  }
  .icon-chevron-left:before {
    content: "\e900";
  }